<template>
 <!-- v-for:"lang in languages" :key"lang" @click="changeLang(lang)" -->
 
<ul>
    <li v-for="lang in languages" :key="lang" @click="changeLang(lang)">
        {{ lang }}
    </li>
</ul>

</template>

<script>

export default {
  name: 'LanguageSwitcher',
  methods: {
        changeLang (lang){
            this.$i18n.locale = lang
        }
    },

  data() {
    return {
      languages :['en','bg']
    }
  },

};
</script>

